// RESET -----------------------------------------------------------------------
a, abbr, acronym, address, applet, article, aside, audio, b, bdi, bdo, big,
blockquote, body, br, button, canvas, caption, center, cite, code, col,
colgroup, data, datalist, dd, del, details, dfn, dialog, dir, div, dl, dt, em,
embed, fieldset, figcaption, figure, font, footer, form, frame, frameset, h1,
h2, h3, h4, h5, h6, header, hr, html, i, iframe, img, input, ins, kbd, label,
legend, li, main, map, mark, meter, nav, noframes, noscript, object, ol,
optgroup, option, output, p, picture, pre, progress, q, rp, rt, ruby, s, samp,
section, select, small, span, strike, strong, sub, summary, sup, svg, table,
tbody, td, template, textarea, tfoot, th, thead, time, tr, tt, u, ul, var,
video, wbr {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

